#root {
  text-align: center;
  background-color: #0a0a14;
  color: #fafafa;
  height: 100%;
  height: 100vh;
  width: 100%;
  width: 100vw;
  overflow: hidden;
  box-sizing: border-box;
  user-select: none;
}

.App {
  overflow: auto;
  height: 100%;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #337;
  display: flex;
  height: 2.5em;
  justify-content: space-between;
}

.select {
  background-color: inherit;
  border: none;
  color: #fafafa;
  padding: 0 0 0 1.5em;
}

.textbox {
  display: flex;
  padding: 0.4em;
  flex: 1;
  max-width: 30em;
}
.textbox > * {
  flex: 1;
  border-radius: 0.5em;
  border: none;
  padding: 0 0.5em;
  outline: none;
}

main {
  padding: 4em 1em;
  height: 100%;
}

.button {
  padding: 1em 1.5em;
  margin-bottom: 2em;
  width: 100%;
  max-width: 40em;
  text-transform: uppercase;
  border-radius: 2em;
  outline: none;
  background-color: #cacaca;
  border: none;
  font-weight: bold;
  transition: all ease 0.5s;
}

.button:hover {
  background-color: #fafafa;
  transition: all ease 0.5s;
}

.table-container {
  flex-direction: column;
  overflow: hidden;
  height: 75%;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  margin: 1em 0;
}

tr > * {
  color: #111;
  padding: 5px;
  min-width: 2em;
}

th {
  background-color: #446;
  color: #fafafa;
}
th:first-child:hover{
  background-color: #225;
}

tr:nth-child(odd) {
  background-color: #d0d0d0;
}

tr:nth-child(even) {
  background-color: #aab;
}

tr:hover {
  background-color: #fff;
  text-shadow: 0 0em 0.2em #555;
  font-weight: bold;
}

table {
  border: none;
  overflow: scroll;
  user-select: none;
  border-collapse: collapse;
  flex: 1;
}
.table {
  overflow: auto;
  align-items: stretch;
  display: flex;
  border-radius: 0.6em;
}
.error{
  color: #f22;
}

.hide {
  display: none !important;
}
